.nav{
    position: fixed;
    top:0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav__contents{
    display:flex;
    justify-content: space-between;
}

.nav__logo{
    position: fixed;
    left:0;
    top: 0;
    height: 90px;
    width: 90px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}

.nav__signout{
    position: fixed;
    right: 20px;
    top: 25px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: red;
    font-weight: 600;
    border: none;
    cursor: pointer;
}

.nav__black{
    background-color: black;
}

