.loginScreen{
    position: relative;
    width: 100%;
    background: url("https://t4.ftcdn.net/jpg/03/49/41/13/240_F_349411371_pLjCyoc2p3myPCALjUmvE3HMia6x0eVg.jpg") center no-repeat;
    background-size: cover;
}

.loginScreen__logo{
    position: fixed;
    left: 0;
    width: 100px;
    object-fit: contain;
    padding-left: 20px;
    
}

.loginScreen__gradient{
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0,0,0,0.2);
    background-image: linear-gradient(
        to top,
        rgba(0,0,0,0.8) 0,
        rgba(0,0,0,0) 60%,
        rgba(0,0,0,0.8)100%
    );
}

.loginScreen__body > h1{
    font-size: 2.125rem;
    margin-bottom: 20px;
    font-weight: 500;
}

.loginScreen__body > h2{
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}

.loginScreen__body >h3{
    font-size: 1.3rem;
    font-weight: 400;
}

.loginScreen__body{
    position: absolute;
    z-index: 1;
    color: #fff;
    top: 30%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    padding: 20px;
    left: 0; 
    right: 0;
}

.loginScreen__button{
    position: fixed;
    right: 20px;
    top: 30px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: red;
    font-weight: 600;
    border: none;
    cursor: pointer;
}

.loginScreen__input > form > input{
    padding: 10px;
    outline-width: 0;
    height: 30px;
    width: 30%; 
    border: none;
    max-width: 600px;
}

.loginScreen__input{
    margin: 20px;
}

.loginScreen__getStarted{
    padding: 15.5px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: red;
    border: none;
    font-weight: 600;
    cursor: pointer;
}